import React from 'react';
import styles from './Article.module.css';
import PDF from '../PDF';
import VideoMP4 from '../videoMP4';

const Page = () => {
  return (
    <div className={styles.article}>
      <div>
        <h3 className={styles.subtitle}>AngryBalls</h3>
        <p>
          Le projet "AngryBalls" est une simulation simple où des billes avec différents comportements interagissent dans un environnement. 
          Chaque bille peut avoir des caractéristiques spécifiques, comme rebondir contre les parois, changer de direction ou accélérer. 
          Le but initial du projet était de montrer comment appliquer des design patterns pour gérer ces comportements de manière flexible.
     </p>
      </div>
       <div className={styles.paragraph_50}>
        
        <h3 className={styles.subtitle}>ECS vs Décorateur</h3>
        <p>
          Dans l'étude qu'on a réalisée avec l'un de mes camarades, nous avons décidé de refaire le projet du cour sur les designs pattern "AngryBalls", où nous avons comparé deux approches pour structurer ce système : le modèle Décorateur, qui était utilisé dans le projet original pour ajouter dynamiquement des comportements aux billes, et le modèle ECS (Entity-Component-System), qui sépare clairement les entités, les composants, et la logique.
          Dans cette vidéo on observe clairement que le modèle ECS est nettement plus performant, avec un framerate (FPS) quatre fois plus élevé que celui du modèle Décorateur.
        </p>
        <p>  
          (Cependant, il est important de noter que les résultats de performance sont lié par différents facteurs, tels que la refonte du projet initial ou le matériel utilisé.)
        </p>
        <p>Pour plus de précisions sur le fonctionnement du modèle ECS, n'hésiter pas à lire le rapport détaillé.</p>
        </div>
      <VideoMP4 filePath={`${process.env.PUBLIC_URL}/data/video/DP2.mp4`} />
      <h3 className={styles.subtitle} >Rapport</h3>
      <PDF filePath={`${process.env.PUBLIC_URL}/data/pdf/Rapport-DP.pdf`} />
    </div>
  );
};

export default Page;
